import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
/**
 * Generated class for the MiRutinaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-mi-rutina',
  templateUrl: 'mi-rutina.html',
})
export class MiRutinaPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public events: Events) {


  events.subscribe('updateRutin', () => {
    this.apiProvider.verificarLogin()
    .then(data => {    
    this.apiProvider.getRutinaUsuario(data.idUsuario)
      .then(data => {
      this.proximoEjercicio = data[1][0];
      this.events.publish('registrarNuevoEjercicio',this.proximoEjercicio );
      this.diasRutina =  (Object.values(data[0]));
       console.log(this.diasRutina);
       console.log(this.proximoEjercicio);


      });
     });
  });



  }

  ionViewDidLoad() {

    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();

    this.apiProvider.verificarLogin()
    .then(data => {    
   

      this.apiProvider.getRutinaUsuario(data.idUsuario)
      .then(data => {
    
      this.proximoEjercicio = data[1][0];
      this.diasRutina =  (Object.values(data[0]));
       console.log(this.diasRutina.length);
      loading.dismissAll();

      console.log(this.proximoEjercicio);


      });



     });


    }

    getPorcentaje(dia){
        let num = 0;
    dia.forEach(function(item){
       if(item.completado) {num++}
    });

    let enviar = ((num*100)/(dia.length)) + '%';
    return enviar;
       
  
  }


  goActividad(){
  //console.log(this.proximoEjercicio.idActividad);
  this.navCtrl.push('ActividadPage',{idActividad:this.proximoEjercicio.idActividad, idR:this.proximoEjercicio.idRutinaActividad, diaNumero:this.proximoEjercicio.diaNumero});
  }


}



